<template>
  <!-- <div class="d-flex flex-row align-start justify-center w-100 h-100"> -->
  <div
    class="profileWrap w-100 h-100"
    style="position: relative; perspective: 1400px"
  >
    <div class="nameCardWrap front" style="z-index: 10">
      <name-card v-on:showAbout="fnShowAbout()"></name-card>
    </div>
    <div class="nameCardWrap" style="z-index: 9">
      <name-card v-on:showAbout="fnShowAbout()"></name-card>
    </div>
    <div class="aboutCardWrap back" style="z-index: 8">
      <about-card></about-card>
    </div>
  </div>
  <!-- </div> -->
</template>
<script>
import nameCard from '../../components/MyCardComponent.vue'
import aboutCard from '../../components/AboutComponent.vue'

export default {
  components: {
    nameCard,
    aboutCard
  },
  data() {
    return {
      cardTop: '',
      cardLeft: '',
      showCard: true,
      showAbout: false,
      preventDupEvent: false // 이벤트 중복방지
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    async fnShowAbout() {
      if (!this.preventDupEvent) {
        this.preventDupEvent = true
        if (
          !document.querySelector('.front').classList.contains('rotateFrontY')
        ) {
          document.querySelector('.front').classList.add('rotateFrontY')
          document.querySelector('.back').classList.add('rotateBackY')
          await this.wait(500)
          document.querySelector('.front').style['z-index'] = '7'
        } else {
          document.querySelector('.front').classList.add('rotateFrontYBack')
          document.querySelector('.back').classList.add('rotateBackYFront')
          await this.wait(500)
          document.querySelector('.front').style['z-index'] = '10'
          await this.wait(500)
          document.querySelector('.front').classList.remove('rotateFrontY')
          document.querySelector('.back').classList.remove('rotateBackY')
          document.querySelector('.front').classList.remove('rotateFrontYBack')
          document.querySelector('.back').classList.remove('rotateBackYFront')
        }
        this.preventDupEvent = false
      }
    },
    wait(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms))
    }
  }
}
</script>
<style>
.profileWrap {
  padding: 10px;
}
.nameCardWrap {
  position: absolute;
  width: clamp(
    348px,
    calc(100vw - 20px),
    calc(calc(100vh - 64px - 20px) * 1.25)
  );
  height: clamp(270px, calc(80vw - 20px), calc(100vh - 64px - 20px));
  padding-right: min(
    clamp(
      179px,
      calc(calc(100vw - 20px) / 2 + 10px),
      calc(calc(calc(100vh - 64px - 20px) * 1.25) / 2 + 10px)
    ),
    510px
  );
  max-width: 1010px;
  max-height: 800px;
  backface-visibility: hidden; /* 뒷면 보이지 않게 */
}

.aboutCardWrap {
  position: absolute;
  width: clamp(
    348px,
    calc(100vw - 20px),
    calc(calc(100vh - 64px - 20px) * 1.25)
  );
  height: clamp(270px, calc(80vw - 20px), calc(100vh - 64px - 20px));
  padding-left: min(
    clamp(
      179px,
      calc(calc(100vw - 20px) / 2 + 10px),
      calc(calc(calc(100vh - 64px - 20px) * 1.25) / 2 + 10px)
    ),
    510px
  );
  max-width: 1010px;
  max-height: 800px;
  backface-visibility: hidden; /* 뒷면 보이지 않게 */
}
.front {
  transform: rotateY(0deg);
}
.back {
  transform: rotateY(-180deg);
}
.slideIn {
  animation: slideIn 0.8s linear forwards;
}
.slideOut {
  animation: slideOut 0.8s linear forwards;
}

@keyframes slideIn {
  to {
    left: 2em;
    opacity: 1;
  }
}
@keyframes slideOut {
  from {
    opacity: 1;
    left: 2em;
  }
  to {
    left: -1em;
    opacity: 0;
  }
}
@keyframes vTohCard {
  50% {
    left: -1em;
    opacity: 0;
  }
  100% {
    left: 2em;
    opacity: 1;
  }
}
@keyframes hTovCard {
  50% {
    left: -1em;
    opacity: 0;
  }
  100% {
    left: 5em;
    opacity: 1;
  }
}

.rotateFrontY {
  animation: rotateFront 1s linear forwards;
}
.rotateFrontYBack {
  animation: rotateFrontBack 1s linear forwards;
}
.rotateBackY {
  animation: rotateBack 1s linear forwards;
}
.rotateBackYFront {
  animation: rotateBackFront 1s linear forwards;
}
@keyframes rotateFront {
  to {
    transform: rotateY(180deg);
  }
}
@keyframes rotateFrontBack {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0);
  }
}
@keyframes rotateBack {
  to {
    transform: rotateY(0deg);
  }
}
@keyframes rotateBackFront {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(-180deg);
  }
}
</style>
