<template>
  <div
    class="skillCard d-flex flex-column text-font flex-wrap justify-start font-weight-bold"
  >
    <div class="skillTop d-flex justify-start">
      <v-card
        class="d-flex justify-center bg-secondary-lighten-1"
        color="primaryBold"
        variant="tonal"
        >#Skill Grade</v-card
      >
    </div>
    <div class="skillInfoWrap">
      <div class="info">
        <div class="d-flex flex-row">
          Grade 1-5
          <v-tooltip location="top" no-click-animation width="400">
            <template v-slot:activator="{ props }">
              <v-icon
                v-bind="props"
                class="ml-2"
                icon="mdi-information-outline"
              ></v-icon>
            </template>
            <div>
              <ul>
                <li>
                  1 - 기본적인 문제 해결 능력을 가지고 있으며, 간단한 오류를
                  해결할 수 있는 수준
                </li>
                <li>
                  2 - 자신이 만든 코드에 대한 이해도가 높으며, 주어진 문제를
                  해결하는 것에 큰 어려움이 없는 수준
                </li>
                <li>
                  3 - 복잡한 문제를 해결할 수 있으며, 남들이 만든 코드도 이해할
                  수 있는 수준
                </li>
                <li>
                  4 - 다양한 문제를 빠르게 해결할 수 있으며, 코드의 효율성과
                  안정성에 대한 이해도가 높은 수준
                </li>
                <li>
                  5 - 최적화된 코드를 작성할 수 있으며, 다른 개발자들의 코드를
                  리뷰하고 개선할 수 있는 수준
                </li>
              </ul>
            </div>
          </v-tooltip>
        </div>
        <v-progress-linear
          indeterminate
          color="primaryBold"
        ></v-progress-linear>
      </div>
    </div>
    <div class="skillContent">
      <div v-for="skill in skillList" :key="skill" :class="skill.type">
        <template v-if="skill.type === 'caption'">
          {{ skill.title }}
        </template>
        <template v-else>
          <div class="progressTitle">{{ skill.title }}</div>
          <div class="progress">
            <v-progress-linear
              :model-value="skill.grade * 20"
              color="primaryBold"
            ></v-progress-linear>
          </div>
          <div>{{ skill.grade }}</div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      skillList: [
        { type: 'caption', title: '#FrontEnd' },
        { type: 'progressWrap', title: 'javascript', grade: 5 },
        { type: 'progressWrap', title: 'HTML', grade: 5 },
        { type: 'progressWrap', title: 'CSS3', grade: 4 },
        { type: 'progressWrap', title: 'jQuery', grade: 5 },
        { type: 'progressWrap', title: 'Vue.js', grade: 4 },
        { type: 'progressWrap', title: 'React', grade: 4 },
        { type: 'progressWrap', title: 'TypeScript', grade: 4 },
        { type: 'caption', title: '#BackEnd' },
        { type: 'progressWrap', title: 'JAVA', grade: 4 },
        { type: 'progressWrap', title: 'C++', grade: 3 },
        { type: 'progressWrap', title: 'Nodejs', grade: 3 },
        { type: 'progressWrap', title: 'Spring', grade: 4 },
        { type: 'progressWrap', title: 'Spting boot', grade: 4 },
        { type: 'caption', title: '#DataBase' },
        { type: 'progressWrap', title: 'Oracle', grade: 4 },
        { type: 'progressWrap', title: 'Mysql', grade: 4 },
        { type: 'progressWrap', title: 'h2DB', grade: 3 },
        { type: 'caption', title: '#Version Control' },
        { type: 'progressWrap', title: 'git', grade: 3 },
        { type: 'progressWrap', title: 'svn', grade: 3 }
      ]
    }
  }
}
</script>
<style>
.skillCard {
  width: 100%;
}
.skillCard .skillTop .v-card {
  border: 2px solid rgb(var(--v-theme-secondary));
  padding-left: 1vw;
  padding-right: 1vw;
  font-size: var(--font-size-lg-mx);
}
.skillCard .skillInfoWrap {
  display: flex;
  flex-direction: row;
  justify-content: end;
  font-size: var(--font-size-sm-mx);
}
.skillCard .skillInfoWrap .info {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skillCard .skillContent .caption {
  font-size: var(--font-size-md-mx);
  margin-top: 2px;
  opacity: 0.7;
}

.skillCard .skillContent .progressWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: var(--font-size-sm-mx);
}
.skillCard .skillContent .progressWrap .progressTitle {
  display: flex;
  width: 20%;
  justify-content: end;
}
.skillCard .skillContent .progressWrap .progress {
  width: calc(80% - 30px);
  display: flex;
  align-items: center;
}

